.clearButton {
  display: flex;
  flex: 1;
  height: 3em;
  background-color: rgb(202, 202, 202);
  color: #000;
  font-size: 1.2rem;
  justify-content: center;
  align-items: center;
  font-weight: lighter;
  cursor: pointer;
  outline: 1px solid rgb(124, 124, 124);
}

.clearButton:active {
  background: rgb(138, 138, 138);
  color: #fff;
}
