.button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 4em;
  flex: 1;
  background-color: white;
  color: #888;
  font-size: 1.5rem;
  outline: 1px solid rgb(124, 124, 124);
  cursor: pointer;
}

.operator {
  background-color: rgb(202, 108, 74);
  color: #fff;
}

.button:active {
  background: rgb(206, 206, 206);
  color: #fff;
}

.operator:active {
  background: rgb(206, 120, 62);
}
